.app-note-main {
  width: 80%;
  margin: 0.5rem auto;
}
.app-note-main .item {
  margin-bottom: 0.3rem;
}
.app-note-main .item .title {
  font-weight: bold;
  font-size: 14px;
  margin: 0.2rem 0 0.1rem 0;
}
.app-note-main .item .line {
  width: 40px;
  height: 1px;
  background-color: #040809;
}
.app-note-main .item .link {
  font-size: 12px;
  margin-top: 0.05rem;
}
.app-note-main .item .link a {
  color: #e71810;
}
.app-note-main .item:last-child {
  margin-bottom: 0;
}
